import {
	mapMutations
} from 'vuex'
export default {
	props: {
		chapter: {

		},
		comicID: {
			default: ''
		}
	},
	data() {
		return {
			reversename: false,
			newer: '',
		};
	},
	created() {
		var last = this.getLastedChapter();
		this.newer = this.dateFormat(last.create_date) + ' ' + last.chapter_name;
	},
	methods: {
		...mapMutations(['setTempData']),
		getLastedChapter() {
			let arr = undefined;
			this.chapter.filter(it => {
				if (arr == undefined) arr = it;
				if (arr.chapter_order_num <= it.chapter_order_num) {
					arr = it;
				}
			})
			return arr;
		},
		reverse() {
			this.reversename = !this.reversename;
			this.$emit('reversechapter', {});
		},
		dateFormat(timestamp) {
			var time = new Date(timestamp * 1000) //先将时间戳转为Date对象，然后才能使用Date的方法
			var year = time.getFullYear(),
				month = time.getMonth() + 1, //月份是从0开始的
				day = time.getDate();

			var ret = year + '-';
			if (month < 10) {
				ret = ret + '0' + month;
			} else {
				ret = ret + month;
			}
			ret += '-';
			if (day < 10) {
				ret = ret + '0' + day;
			} else {
				ret = ret + day;
			}
			//add0()方法在后面定义
			return ret
		},
		goread(item, index) {
			var that = this;
			// console.log(item)
			// 存储中转数据到vuex
			this.setTempData({
				'key': 'temp_read',
				data: item
			});
			uni.navigateTo({
				url: '/pages/readcomic/readcomic?id=' + that.comicID + '&chapter=' + item.chapter_order_num
			})
		}
	}
}
